<template>
  <div id='head'>
    <img src="../assets/logo.png">
    <h1>{{ msg }}</h1>
    <ul id="navigateBar">
      <li class="singleNavigate" v-for="item in items">
        <router-link :to="item.link">{{ item.content }}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'head',
  data () {
    return {
      msg: 'i am allen, a software engineer!',
      items: [
        {
          link: '/',
          content: '主页'
        },
        {
          link: '/article',
          content: '文章'
        },
        {
          link: '/lab',
          content: '实验室'
        }
      ]
    }
  }
}
</script>

<style>
#head {
  width: 100%;
  height: 500px;
}
</style>



